.heading {
  font-size: 48px;
  line-height: 1;
  position: relative;
  text-transform: uppercase;
  font-weight: 300;
  width: max-content;

  &::before,
  &::after {
    content: "";
    display: block;
    position: absolute;
    height: 2px;
    width: calc(50% - 20px);
    top: calc(100% + 5px);
    background-color: #00aaef;
  }
  
  &::before {
    left: 0;
  }

  &::after {
    right: 0;
  }

  &__crown {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    top: calc(100% + 4px);
    margin: auto;
    background-image: url("../images/crown.svg");
    width: 20px;
    height: 13px;
  }
}

@media (max-width: 1023px) {
  .heading {
    font-size: 38px;
  }
}
